<template>
    <div>
      <!--home轮播图-->
      <mt-swipe :auto="4000"  class="mint-swipe">
        <mt-swipe-item v-for="(img,index) in imgList" :key="index">
          <a :href="img.img"><img :src="img.img"></a>
        </mt-swipe-item>
      </mt-swipe>
      <!--九宫格-->
      <div class="mui-content">
        <ul class="mui-table-view mui-grid-view mui-grid-9">
          <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link :to="{name:'news.list'}">
            <span class="mui-icon mui-icon-home"></span>
            <div class="mui-media-body">新闻资讯</div>
            </router-link>
          </li>
          <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link :to="{name:'photo.share'}">
              <span class="mui-icon mui-icon-email"></span>
              <div class="mui-media-body">图文分享</div>
            </router-link>
          </li>
          <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link :to="{name:'goods.list'}">
              <span class="mui-icon mui-icon-chatbubble"></span>
              <div class="mui-media-body">商品展示</div>
            </router-link>
          </li>
          <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><a href="#">
            <span class="mui-icon mui-icon-location"></span>
            <div class="mui-media-body">留言反馈</div></a></li>
          <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><a href="#">
            <span class="mui-icon mui-icon-search"></span>
            <div class="mui-media-body">搜索资讯</div></a></li>
          <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><a href="#">
            <span class="mui-icon mui-icon-phone"></span>
            <div class="mui-media-body">联系我们</div></a></li>
        </ul>
      </div>
    </div>
</template>

<script>
    export default {
      name: "home",
      data(){
        return{
          imgList:[]//轮播图地址列表
        }
      },
      created(){
        this.$axios.get(this.$host+'/api/getswipe')
        .then(res=>{
          // console.log(res.data.data)
          this.imgList=res.data.data
        })
        .catch(err=>{
          console.log(err)
        });
      }
    }
</script>

<style scoped>
.mint-swipe{
  height: 200px;
  background-color: #b42c29;
}
img{
  width: 100%;
  height: 100%;
}
  /*九宫格样式*/
.mui-grid-view.mui-grid-9{
  background-color: white;
  border-top: 0px;
  margin-top: 0px;
}
.mui-grid-view.mui-grid-9 .mui-table-view-cell{
  border: 0;
}
  /*去除九宫格字体图标*/
.mui-icon-home:before,
.mui-icon-email:before,
.mui-icon-chatbubble:before,
.mui-icon-location:before,
.mui-icon-search:before,
.mui-icon-phone:before{
  content: '';
}
.mui-icon-home{
  background-image: url("../../../static/img/news.png");
  background-repeat: round;
}
.mui-icon-email{
  background-image: url("../../../static/img/share.png");
  background-repeat: round;
}
/*自定义图标*/
.mui-icon-chatbubble{
  background-image: url("../../../static/img/shop.png");
  background-repeat: round;
}
.mui-icon-location{
  background-image: url("../../../static/img/say.png");
  background-repeat: round;
}
.mui-icon-search{
  background-image: url("../../../static/img/search.png");
  background-repeat: round;
}
.mui-icon-phone{
  background-image: url("../../../static/img/contact.png");
  background-repeat: round;
}
.mui-icon{
  height: 50px;
  width: 50px;
}
</style>



